html {
  box-sizing: border-box;
  font-family: Arial Rounded MT Bold, Helvetica Rounded, Arial, sans-serif;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  min-height: 100vh;
  background: linear-gradient(45deg, #ff1a66 0%, rgba(255, 26, 102, 0) 70%), linear-gradient(135deg, #0643f9 10%, rgba(6, 67, 249, 0) 80%), linear-gradient(225deg, #0df259 10%, rgba(13, 242, 89, 0) 80%), linear-gradient(315deg, #f99e1f 100%, rgba(249, 158, 31, 0) 70%);
  margin: 0;
}

h2 {
  margin-top: 0;
  padding-top: .8em;
}

nav {
  perspective: 600px;
  position: relative;
}

.cool > li > a {
  color: #ff0;
  background: rgba(0, 0, 0, .2);
  border-radius: 5px;
  margin: 20px;
  padding: 10px 20px;
  font-size: 20px;
  text-decoration: none;
  display: inline-block;
}

nav ul {
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.cool > li {
  justify-content: center;
  display: flex;
  position: relative;
}

.dropdown {
  opacity: 0;
  will-change: opacity;
  border-radius: 2px;
  padding: 20px;
  transition: all .5s;
  display: none;
  position: absolute;
  top: -20px;
  overflow: hidden;
  transform: translateY(100px);
}

.trigger-enter .dropdown {
  display: block;
}

.trigger-enter-active .dropdown {
  opacity: 1;
}

.dropdownBackground {
  width: 100px;
  height: 100px;
  transform-origin: 50% 0;
  opacity: 0;
  background: #fff;
  border-radius: 4px;
  justify-content: center;
  transition: all .3s, opacity .1s, transform .2s;
  display: flex;
  position: absolute;
  box-shadow: 0 50px 100px rgba(50, 50, 93, .1), 0 15px 35px rgba(50, 50, 93, .15), 0 5px 15px rgba(0, 0, 0, .1);
}

.dropdownBackground.open {
  opacity: 1;
}

.arrow {
  width: 20px;
  height: 20px;
  background: #fff;
  display: block;
  position: absolute;
  transform: translateY(-50%)rotate(45deg);
}

.bio {
  min-width: 500px;
  justify-content: center;
  align-items: center;
  line-height: 1.7;
  display: flex;
}

.bio img {
  float: left;
  margin-right: 20px;
}

.courses {
  min-width: 300px;
}

.courses li {
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  padding: 10px 0;
  display: block;
}

.dropdown a {
  color: #ffc600;
  text-decoration: none;
}

a.button {
  color: #fff;
  background: #000;
  margin-bottom: 10px;
  padding: 10px;
  display: block;
}

.button[href*="twitter"] {
  background: #019fe9;
}

.button[href*="facebook"] {
  background: #3b5998;
}

.button[href*="courses"] {
  background: #ffc600;
}

